import { Menu } from 'antd';
import React, { useEffect, useState } from 'react';
import { Link } from "react-router-dom";
import { CalculatorOutlined, ContainerOutlined, DashboardTwoTone, DollarCircleOutlined, FileTextOutlined, FundProjectionScreenOutlined, FundViewOutlined, PartitionOutlined, SolutionOutlined, UserAddOutlined, UserOutlined } from '@ant-design/icons/lib';
import SubMenu from 'antd/lib/menu/SubMenu';
import { useStoreActions, useStoreState } from '../../store/hooks/easyPeasy';

export function Sidebar() {
	// console.log(window.location.pathname)
	const fetchpartnerProfile = useStoreActions(state => state.common.fetchpartnerProfile);
	const partnerProfile = useStoreState(state => state.common.partnerProfile);
	useEffect(() => {
		fetchpartnerProfile();
	}, []);

	const [pathValue, setpathValue] = useState<any>("1")
	const [mainpathValue, setmainpathValue] = useState<any>(null)

	const sideClick = (value) => {
		// console.log(value)
		setpathValue(value.key)
	}



	// console.log(pathValue)

	return <Menu theme="dark" defaultSelectedKeys={[pathValue]} mode="inline" selectedKeys={[pathValue]} onClick={sideClick}>
		<Menu.Item key="1" icon={<DashboardTwoTone />}>
			<Link to="/" className="nav-text">Dashboard</Link>
		</Menu.Item>
		{partnerProfile?.designation == "APIP" &&
			<>
				<SubMenu key={"sub1"} icon={<PartitionOutlined />} title="Partner" >
					<Menu.Item key="3" icon={<FileTextOutlined />}>
						<Link to="/order-form" className="nav-text">Order Form</Link>
					</Menu.Item>
				</SubMenu>
				<SubMenu key={"reportApip"} icon={<PartitionOutlined />} title="Report" >
					<Menu.Item key="11" icon={<FileTextOutlined />}>
						<Link to="/bill-payment-apip" className="nav-text">Bill Payment</Link>
					</Menu.Item>
					<Menu.Item key="16" icon={<FileTextOutlined />}>
						<Link to="/order-status" className="nav-text">Order Status</Link>
					</Menu.Item>
				</SubMenu>
			</>
		}
		{partnerProfile?.designation == "PIP" &&
			<>
				<SubMenu key={"isp"} icon={<PartitionOutlined />} title="ISP" >
					<Menu.Item key="19" icon={<FileTextOutlined />}>
						<Link to="/isp-client-add" className="nav-text">Client Add</Link>
					</Menu.Item>
					<Menu.Item key="21" icon={<FileTextOutlined />}>
						<Link to="/isp-client-list" className="nav-text">Client List</Link>
					</Menu.Item>
					<Menu.Item key="24" icon={<FileTextOutlined />}>
						<Link to="/isp-bill-list" className="nav-text">Bill List</Link>
					</Menu.Item>
				</SubMenu>
				<SubMenu key={"reportPip"} icon={<PartitionOutlined />} title="Report" >
					<Menu.Item key="12" icon={<FileTextOutlined />}>
						<Link to="/bill-payment-pip" className="nav-text">Bill Payment</Link>
					</Menu.Item>
				</SubMenu>
			</>
		}
		{partnerProfile?.designation == "SDL" &&
			<>
				<SubMenu key={"reportPip"} icon={<PartitionOutlined />} title="Report" >
					<Menu.Item key="17" icon={<FileTextOutlined />}>
						<Link to="/sdl-order-status" className="nav-text">Order List (Status)</Link>
					</Menu.Item>
					<Menu.Item key="13" icon={<FileTextOutlined />}>
						<Link to="/bill-payment-sdl" className="nav-text">Bill Payment</Link>
					</Menu.Item>
					<Menu.Item key="14" icon={<FileTextOutlined />}>
						<Link to="/bill-payment-sdl-monthly" className="nav-text">Monthly Bill Payment</Link>
					</Menu.Item>
				</SubMenu>
			</>
		}
		{/* <Menu.Item key="5" icon={<FundProjectionScreenOutlined />}>
				<Link to="/payable-bill" className="nav-text">Payable Bill</Link>
			</Menu.Item>
			<Menu.Item key="6" icon={<FundViewOutlined />}>
				<Link to="/paid-bill" className="nav-text">Paid Bill</Link>
			</Menu.Item> */}

		{partnerProfile?.partnerRoles?.includes('ROLE_SUPER_ADMIN') && <>
			<SubMenu key="sub2" icon={<SolutionOutlined />} title="Administration" >
				<Menu.Item key="2" icon={<UserAddOutlined />}>
					<Link to="/pending" className="nav-text">Pending Partner</Link>
				</Menu.Item>
				<Menu.Item key="18" icon={<UserAddOutlined />}>
					<Link to="/approved-partner" className="nav-text">Approved Partner</Link>
				</Menu.Item>
				<SubMenu key="sub8" icon={<ContainerOutlined />} title="Order List" >
					<Menu.Item key="4" icon={<ContainerOutlined />}>
						<Link to="/order-list" className="nav-text">General</Link>
					</Menu.Item>
					<Menu.Item key="8" icon={<ContainerOutlined />}>
						<Link to="/preorder-list" className="nav-text">Pre-Approve</Link>
					</Menu.Item>
				</SubMenu>
				<Menu.Item key="10" icon={<ContainerOutlined />}>
					<Link to="/bill-disbursment" className="nav-text">Bill Disbursment</Link>
				</Menu.Item>
				<SubMenu key="sub3" icon={<CalculatorOutlined />} title="Bill Generate" >
					<Menu.Item key="7" icon={<CalculatorOutlined />}>
						<Link to="/bill-generate-monthly" className="nav-text">Monthly</Link>
					</Menu.Item>
					<Menu.Item key="9" icon={<CalculatorOutlined />}>
						<Link to="/bill-generate-yearly" className="nav-text">Yearly</Link>
					</Menu.Item>
				</SubMenu>
				<SubMenu key="sub9" icon={<CalculatorOutlined />} title="ISP" >
					<Menu.Item key="20" icon={<CalculatorOutlined />}>
						<Link to="/isp-package-create" className="nav-text">Create Package</Link>
					</Menu.Item>
					<Menu.Item key="22" icon={<CalculatorOutlined />}>
						<Link to="/isp-client-list-sdl" className="nav-text">Client List</Link>
					</Menu.Item>
					<Menu.Item key="23" icon={<CalculatorOutlined />}>
						<Link to="/isp-bill-generate" className="nav-text">Bill Generate</Link>
					</Menu.Item>
					<Menu.Item key="25" icon={<CalculatorOutlined />}>
						<Link to="/isp-bill-list-sdl" className="nav-text">Bill List</Link>
					</Menu.Item>					
					<Menu.Item key="isp-bill-list-monthly-sdl" icon={<CalculatorOutlined />}>
						<Link to="/isp-bill-list-monthly-sdl" className="nav-text">Bill List Monthly</Link>
					</Menu.Item>					
					<Menu.Item key="isp-bill-list-daily-sdl" icon={<CalculatorOutlined />}>
						<Link to="/isp-bill-list-daily-sdl" className="nav-text">Bill List Day</Link>
					</Menu.Item>
					<SubMenu key="billdisburse" icon={<CalculatorOutlined />} title="Bill Disburse" >				
					<Menu.Item key="isp-bill-disburstment-sdl" icon={<CalculatorOutlined />}>
						<Link to="/isp-bill-disburstment-sdl" className="nav-text">Create</Link>
					</Menu.Item>					
					<Menu.Item key="isp-bill-disburstment-list-sdl" icon={<CalculatorOutlined />}>
						<Link to="/isp-bill-disburstment-list-sdl" className="nav-text">List</Link>
					</Menu.Item>
					</SubMenu>	
				</SubMenu>
			</SubMenu>
		</>
		}

		{partnerProfile?.partnerRoles?.includes('ROLE_CUSTOMER_SUPPORT') && <>
			<SubMenu key="sub2" icon={<SolutionOutlined />} title="Customer Support" >

				<SubMenu key="sub8" icon={<ContainerOutlined />} title="Order List" >
					<Menu.Item key="4" icon={<ContainerOutlined />}>
						<Link to="/order-list" className="nav-text">General</Link>
					</Menu.Item>

				</SubMenu>
				<Menu.Item key="10" icon={<ContainerOutlined />}>
					<Link to="/bill-disbursment" className="nav-text">Bill Disbursment</Link>
				</Menu.Item>
				<SubMenu key="sub3" icon={<CalculatorOutlined />} title="Bill Generate" >
					<Menu.Item key="7" icon={<CalculatorOutlined />}>
						<Link to="/bill-generate-monthly" className="nav-text">Monthly</Link>
					</Menu.Item>
					<Menu.Item key="9" icon={<CalculatorOutlined />}>
						<Link to="/bill-generate-yearly" className="nav-text">Yearly</Link>
					</Menu.Item>
				</SubMenu>
				<SubMenu key="sub9" icon={<CalculatorOutlined />} title="ISP" >
					<Menu.Item key="22" icon={<CalculatorOutlined />}>
						<Link to="/isp-client-list-sdl" className="nav-text">Client List</Link>
					</Menu.Item>
					<Menu.Item key="23" icon={<CalculatorOutlined />}>
						<Link to="/isp-bill-generate" className="nav-text">Bill Generate</Link>
					</Menu.Item>
					<Menu.Item key="25" icon={<CalculatorOutlined />}>
						<Link to="/isp-bill-list-sdl" className="nav-text">Bill List</Link>
					</Menu.Item>
				</SubMenu>
			</SubMenu>
		</>
		}
		{/* <Menu.Item key="15" icon={<UserOutlined />}>
			<Link to="/profile" className="nav-text">Profile</Link>
		</Menu.Item> */}



	</Menu>
}
